import { createStore, combineReducers, compose,applyMiddleware } from 'redux';
import ReduxPromise from 'redux-promise';
import {GET_SPACE_DATA,SET_SPACE_DATA,GET_PAD_DETAILS} from '../Utils';


const { NODE_ENV } = process.env;
const isDevelopment = NODE_ENV === 'development';

const reducers = {
    spaceData: (oldState = {}, action) => {
        const { type } = action;
        switch (type) {
            case GET_SPACE_DATA: return action.payload.data;
            
            case SET_SPACE_DATA: return '';
            default:
                return oldState;
        }
    },
    padData : (state={},action)=>{
        const {type} = action;
        console.log(action);
        switch(type){
            case GET_PAD_DETAILS: return action.payload.data;
            default:
                return state;
        }
    }
};

const slices = combineReducers({ ...reducers });

const  composeEnhancers = isDevelopment && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({ trace: true, traceLimit: 25 })
    : compose;

const createStoreWithMiddleware = applyMiddleware(ReduxPromise)(createStore);

const store = createStoreWithMiddleware(slices,composeEnhancers);

export default store;
